.modal {
    display: flex;
    align-items: center;
    justify-content: center;
}
.modal-main {
    position: absolute;
    width: 400px;
    background-color: white;
    border: 1px solid transparent;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgb(0 0 0 / 32%), 0 8px 40px rgb(0 0 0 / 40%);
    padding: 24px;
}
.modal-span {
    font-weight: 800;
}