.folder-item {
    cursor: pointer;
    background-color: #fff;
    border: 1px solid;
    -webkit-border-radius: 6px;
    border-radius: 6px;
    margin: 7px;
    display: flex;
    align-items: center;
    flex: 1 1 226px;
    min-width: 226px;
    font-size: 15px;
    font-weight: 600;
    text-decoration: none;
    color: black;
    padding-right: 16px;
}

.folder-item:hover {
    color: #525252;
}

.folder img {
    height: 50px;
    padding: 0 16px;
}

.file-flex {
    height: 65px;
    display: flex;
    flex-wrap: wrap;
}
.react-contextmenu {
    box-shadow: 0px 3px 7px rgb(0 0 0 / 32%);
}
.contextmenu-item{
    cursor: pointer;
    min-width: 200px;
    max-width: 250px;
    text-align: center;
    padding: 5px 10px;
    background: white;
}
.contextmenu-item:hover {
    background: #e8f0fe;
}
