form {
    display: flex;
    justify-content: space-between;
    flex-flow: column;
    max-width: 400px;
    min-width: 305px;
}

.center-form {
    margin: auto;
}

form input {
    min-height: 32px;
    font-size: 13px;
    margin-top: 20px;
}


.submit_button {
    margin-top: 14px;
    background: #4285f4;
    color: rgba(255,255,255,1);
    border: 1px solid transparent;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    border-radius: 2px;
    min-width: 64px;
    padding: 0 16px;
    text-align: center;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 30px;
    min-height: 32px;
}