@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,700;1,600&display=swap);
html {
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
}

body{
    margin: 0;
    background: white;
}

.link {
    text-decoration: none;
    margin-left: 20px;
    color: black;
}
.folder-item {
    cursor: pointer;
    background-color: #fff;
    border: 1px solid;
    border-radius: 6px;
    margin: 7px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex: 1 1 226px;
            flex: 1 1 226px;
    min-width: 226px;
    font-size: 15px;
    font-weight: 600;
    text-decoration: none;
    color: black;
    padding-right: 16px;
}

.folder-item:hover {
    color: #525252;
}

.folder img {
    height: 50px;
    padding: 0 16px;
}

.file-flex {
    height: 65px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.react-contextmenu {
    box-shadow: 0px 3px 7px rgb(0 0 0 / 32%);
}
.contextmenu-item{
    cursor: pointer;
    min-width: 200px;
    max-width: 250px;
    text-align: center;
    padding: 5px 10px;
    background: white;
}
.contextmenu-item:hover {
    background: #e8f0fe;
}

.modal {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.modal-main {
    position: absolute;
    width: 400px;
    background-color: white;
    border: 1px solid transparent;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgb(0 0 0 / 32%), 0 8px 40px rgb(0 0 0 / 40%);
    padding: 24px;
}
.modal-span {
    font-weight: 800;
}
form {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-flow: column;
            flex-flow: column;
    max-width: 400px;
    min-width: 305px;
}

.center-form {
    margin: auto;
}

form input {
    min-height: 32px;
    font-size: 13px;
    margin-top: 20px;
}


.submit_button {
    margin-top: 14px;
    background: #4285f4;
    color: rgba(255,255,255,1);
    border: 1px solid transparent;
    box-sizing: border-box;
    cursor: pointer;
    display: inline-block;
    border-radius: 2px;
    min-width: 64px;
    padding: 0 16px;
    text-align: center;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 30px;
    min-height: 32px;
}
